<template>
  <div id="room-preview">
    <div class="room-options">
      <input type="file" class="upload-banner-image" id="uploadInput" @change="readURL">
      <select class="select-size" v-model="selectedSize">
        <option :value="size.value" v-for="size in data.sizes">{{size.display}}</option>
      </select>
      <select class="select-room" v-model="selectedRoom">
        <option :value="room.value" v-for="room in data.rooms">{{room.display}}</option>
      </select>
      <br>
    </div>
    <div class="room-layers">
      <span id="cloud-layers" v-html='cloud'></span>
      <template v-for="combo in combinations">
        <span v-html="cloudinaryImg(backgroundImage,combo.size.value,combo.room.value)" style="display: none"></span>
      </template>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
import cloudinary from 'cloudinary';

export default {
  data: function() {
    return {
      selectedSize: 250,
      backgroundImage: 'test_patterns:eye_balls',
      selectedRoom: 'kids',
      data: {
        sizes: [
          {
            value: 63,
            display: '6"x6"'
          },
          {
            value: 83,
            display: '8"x8"'
          },
          {
            value: 125,
            display: '12"x12"'
          },
          {
            value: 250,
            display: '24"x24"'
          }
        ],
        rooms: [
          {
            value: 'kids',
            display: 'Kids'
          },
          {
            value: 'nursery',
            display: 'Nursery'
          },
          {
            value: 'industrial',
            display: 'Industrial'
          },
          {
            value: 'office',
            display: 'Office'
          }
        ]
      }
    };
  },
  methods: {
    readURL() {
      let input = document.getElementById('uploadInput');
      if (input.files && input.files[0]) {
        var reader = new FileReader();
        reader.onload = e => {
          cloudinary.v2.uploader.unsigned_upload(
            e.target.result,
            'pv3o8tmu',
            { folder: 'test_patterns/' },
            (error, result) => {
              let original = result.public_id;
              this.backgroundImage = original.replace('/', ':'); // Cloudinary urls require : instead of / for folders
            }
          );
        };
        reader.readAsDataURL(input.files[0]);
      }
    },
    cloudinaryImg(background, size, room) {
      return cloudinary.image(`test_rooms/wall_${room}`, {
        transformation: [
          {
            overlay: background,
            flags: 'tiled',
            opacity: 85,
            effect: 'multiply',
            width: size
          },
          {
            effect: 'blur:10'
          },
          {
            overlay: `test_rooms:furniture_${room}`
          },
          {
            overlay: 'test_rooms:noise',
            effect: 'multiply'
          },
          {
            overlay: `test_rooms:shadows_${room}`,
            effect: 'multiply'
          },
          {
            overlay: `test_rooms:lighting_${room}`,
            effect: 'multiply'
          }
        ]
      });
    },
    // TODO: Implement cloudinary upload widget?
    openWidget() {
      cloudinary.openUploadWidget(
        { cloud_name: 'rfathead', upload_preset: 'f05u9z4r' },
        (error, result) => {
          console.log(error, result);
        }
      );
    }
  },
  computed: {
    cloud() {
      return this.cloudinaryImg(
        this.backgroundImage,
        this.selectedSize,
        this.selectedRoom
      );
    },
    combinations() {
      let combos = [];
      for (var i = 0; i < this.data.rooms.length; i++) {
        for (var z = 0; z < this.data.sizes.length; z++) {
          combos.push({ room: this.data.rooms[i], size: this.data.sizes[z] });
        }
      }
      return combos;
    }
  }
};
</script>
<style <style lang="scss" scoped>

</style>



